.blockForContent {
  width: calc(25% - 12px);
  height: 272px;
  padding: 10px;
  box-sizing: border-box;
  position: relative;
  box-shadow: 4px 8px 16px rgba(0, 0, 0, 0.2);
  font-family: "PT Sans", sans-serif;
  border-radius: 5%;
}

.raiting {
  position: absolute;
  top: 20px;
  left: 36px;
  color: #f2c94c;
  font-weight: 700;
  font-size: 10px;
  margin: 0;
  padding: 0;
}

.TitleOfThing {
  position: absolute;
  bottom: 40px;
  left: 16px;
  color: #59606d;
  font-size: 14px;
  height: 18px;
  font-weight: 400;
  font-style: normal;
}

.Price {
  position: absolute;
  bottom: 16px;
  left: 16px;
  font-weight: 700;
}

.content_picture {
  position: absolute;
  left: 61px;
  top: 18px;
  width: 142px;
  height: 180px;
}

.img_starForRaiting {
  position: absolute;
  left: 15px;
  top: 17px;
  margin: 0;
  padding: 0;
}

.ForOrder {
  position: absolute;
  right: 18px;
  top: 17px;
  width: 12px;
  height: 13px;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

/* mobile */

@media (max-width: 1900px) {
  .blockForContent {
    width: calc(33% - 12px);
  }
}


@media (max-width: 1600px) {
  .blockForContent {
    width: calc(40% - 12px);
  }

}

@media (min-width: 900px) and (max-width: 1300px) {
  .blockForContent {
    width: calc(55% - 12px);
  }
}

@media (min-width: 550px) and (max-width: 900px) {
  .blockForContent {
    width: calc(90% - 12px);
  }
}

@media (min-width: 0px) and (max-width: 550px) {
  .blockForContent {
    width: 100%;
    margin: 0px;

  }
}

